	export default {
		data() {
			return {
                style: {
                	pageHeight: 0,
                	contentViewHeight: 0
                },
                user: {
                    userName: '',
                    userPhone: '',
                    userContent: '',
                    userUrl: '/static/140.png'
                }
                
			}
		},
		methods: {
            upload(){
                let that = this;
                let imageInfo = {};
                let userToken = this.$store.state.userInfo.loginToken;
                let userId = this.$store.state.userInfo.userId;
                uni.chooseImage({
                    count: 1,
                    sizeType: ['compressed'],
                    success(res) {
                        const tempFilePaths = res.tempFilePaths;
                        uni.uploadFile({
                            url: that.websiteUrl + '/api/user/uploadImage',
                            header:{
                                "Authorization": userToken
                            },
                            filePath: tempFilePaths[0],
                            name: 'file',
                            formData: {
                                'userId': userId
                            },
                            success: (res) => {
                                let data = JSON.parse(res.data);
                                let imageUrl = data.user.userPortrait;
                                imageInfo.networkImage = that.websiteUrl + '/' + imageUrl;
                                if (data.status == 0) {
                                    uni.downloadFile({
                                        url: that.websiteUrl + '/' + imageUrl,
                                        success: function (res) {
                                            let tempFilePath = res.tempFilePath;
                                            uni.saveFile({
                                                tempFilePath: tempFilePath,
                                                success: function (res) {
                                                    let savedFilePath = res.savedFilePath;
                                                    imageInfo.localImage = savedFilePath;
                                                    that.$store.commit('storeImage',imageInfo);
                                                    that.user.userUrl = savedFilePath;
                                                    uni.showToast({
                                                        title: '上传成功',
                                                        mask: true
                                                    });
                                                    setTimeout(function(){
                                                        uni.hideToast();
                                                    },2000)
                                                }
                                            })
                                        }
                                    })
                                } else if (res.data.status == 1001) {
                                    uni.showToast({
                                        title: res.data.message,
                                        icon: 'none',
                                        mask: true
                                    });
                                    setTimeout(function () {
                                        uni.hideToast();
                                        uni.navigateTo({
                                            url:"/pages/login/login",
                                            animationType:"slide-in-right"
                                        });
                                    }, 300)
                                } else {
                                    uni.showToast({
                                        title: data.message,
                                        icon: 'none',
                                        mask: true
                                    });
                                    setTimeout(function(){
                                        uni.hideToast();
                                    },2000)
                                }
                            }
                        })
                    }
                })
            },
            toChangeName(){
                uni.navigateTo({
                    url: '/pages/changeName/changeName',
                    animationType: 'slide-in-right'
                });
            },
            toSign(){
                uni.navigateTo({
                    url: '/pages/autograph/autograph',
                    animationType: 'slide-in-right'
                });
            }
		},
        onShow() {
            const info = uni.getSystemInfoSync();
            this.style.pageHeight = info.windowHeight;
            this.style.contentViewHeight = info.windowHeight; //像素
            this.$set(this.user,'userUrl',this.$store.state.userImage.localImage);
            this.$set(this.user,'userName',this.$store.state.userInfo.userName);
            this.$set(this.user,'userPhone',this.$store.state.userInfo.userPhone);
            this.$set(this.user,'userContent',this.$store.state.userInfo.userText);
            this.$set(this.user,'userContent',this.$store.state.userInfo.userText);
        }
	}
